import { IDictionary, IThemeModule } from '@msdyn365-commerce/core-internal';
import {
    resolveRef,
    safeFileExists,
    safeGetAllFilesPath,
    safeGetAllFilesPathSync,
    safeReadJson
} from '@msdyn365-commerce/utilities-internal';
import { merge as _merge } from 'lodash';
import path, { dirname, join, resolve } from 'path';
import { getModulesDAPIHelperSingleton } from './module-dapi-helper';
import { IModuleRegistration, THEME_MODULE_TYPE } from './module-registration-model';
import {
    getModuleName,
    IModuleDefaultData,
    IModuleDefinition,
    IModuleProperty,
    IThemeDefinition,
    keystonePaths,
    modulePreviewDataInTheme,
    THEME_MODULE,
    themePreviewData
} from './utils/models';

const classNameDefnProperty = {
    friendlyName: 'Default CSS Class Name(s)',
    description: 'Provides a way to set configurable default css class(es) to apply to your module.',
    type: 'css'
};

const getThemePreviewObjForPaths = async (themePreviewPaths: string[]): Promise<modulePreviewDataInTheme> => {
    const themePreviews: modulePreviewDataInTheme = {};
    for (const previewPath of themePreviewPaths) {
        const previewObj = await safeReadJson<IModuleDefaultData>(previewPath);
        if (previewObj) {
            const previewName = path.basename(previewPath).replace('.preview.json', '');
            const moduleName = path.basename(path.dirname(previewPath));
            if (!themePreviews[moduleName]) {
                themePreviews[moduleName] = {};
            }
            themePreviews[moduleName][previewName] = previewObj;
        }
    }
    return themePreviews;
};

const getThemePreviews = async (themeModuleBinder: IModuleRegistration) => {
    let themePreviews: modulePreviewDataInTheme | undefined;
    const themeDir = themeModuleBinder.moduleDirectory || path.dirname(themeModuleBinder.definitionPath);
    const previewsPattern = path.join(themeDir, THEME_MODULE.PREVIEWS, 'modules', '**', '*.preview.json');
    const themePreviewPaths = await safeGetAllFilesPath(previewsPattern);
    themePreviews = await getThemePreviewObjForPaths(themePreviewPaths);
    return themePreviews;
};

const getAllThemePreviews = async (themeName: string, themeModuleBinder: IModuleRegistration) => {
    let themePreview: modulePreviewDataInTheme = {};
    let moduleThemePreview: modulePreviewDataInTheme = {};
    let partnerThemePreview: modulePreviewDataInTheme = {};

    // get parent theme module's preview data
    const parentDefinitionPath = themeModuleBinder.parentDefinitionPath;
    if (parentDefinitionPath) {
        const dapiHelper = getModulesDAPIHelperSingleton();
        const parentThemeName = dapiHelper.getModuleName(parentDefinitionPath);
        const parentThemeBinder = dapiHelper.getModuleBinder(parentThemeName);
        themePreview = await getThemePreviews(parentThemeBinder);
    }

    // Read theme module previews and merge
    moduleThemePreview = await getThemePreviews(themeModuleBinder);
    Object.keys(moduleThemePreview || {}).forEach(moduleName => {
        // merge previews data of each module under the theme
        themePreview[moduleName] = _merge({}, themePreview[moduleName], moduleThemePreview[moduleName]);
    });

    // Read partner level previews and merge
    const partnerThemePreviewsPaths =
        (await safeGetAllFilesPath(
            path.join(keystonePaths.KEYSTONE_THEMES_DIR, themeName, THEME_MODULE.PREVIEWS, 'modules', '**', '*.preview.json')
        )) || [];
    partnerThemePreview = await getThemePreviewObjForPaths(partnerThemePreviewsPaths);
    Object.keys(partnerThemePreview).forEach(moduleName => {
        themePreview[moduleName] = _merge({}, themePreview[moduleName], partnerThemePreview[moduleName]);
    });
    return Object.keys(themePreview).length > 0 ? themePreview : undefined;
};

export const getThemesDAPIList = async (
    moduleRegistrations: IDictionary<IModuleRegistration>,
    parsedDefinitions: IDictionary<IModuleDefinition>,
    dapiThemePreviewData: themePreviewData
): Promise<IThemeModule[]> => {
    const themeModules: IThemeModule[] = [];
    if (themeModules.length) {
        return themeModules;
    }

    const themesPromises = (Object.keys(moduleRegistrations) || [])
        .filter(moduleType => moduleRegistrations[moduleType].$type === THEME_MODULE_TYPE)
        .map(async moduleType => {
            const moduleBinder = moduleRegistrations[moduleType];

            // tslint:disable-next-line:no-unnecessary-local-variable
            const parsedModuleDef = parsedDefinitions[moduleBinder.name];
            const themeModule = <IThemeDefinition>(<unknown>moduleRegistrations[moduleBinder.name]);

            themeModule.pages = await getThemePages(moduleBinder);
            themeModule.segments = await getThemeSegments(moduleBinder);

            themeModule.definitionExtensions = await getThemeDefinitionExtensions(themeModule.name, moduleBinder);
            const themePreviews: modulePreviewDataInTheme | undefined = await getAllThemePreviews(themeModule.name, moduleBinder);
            if (themePreviews) {
                dapiThemePreviewData[themeModule.name] = themePreviews;
            }

            const themeStylePresets = await getDefaultThemeStylePreset(themeModule.name, moduleBinder);
            themeModule.styles = {
                definition: themeStylePresets,
                presets:
                    Object.entries(themeStylePresets).length !== 0 ? await getThemeStylePresetInstances(themeModule.name, moduleBinder) : {}
            };

            return {
                description: parsedModuleDef.description,
                friendlyName: parsedModuleDef.friendlyName,
                name: parsedModuleDef.name,
                type: parsedModuleDef.type,
                pages: themeModule.pages,
                segments: themeModule.segments || [],
                definitionExtensions: themeModule.definitionExtensions,
                styles: themeModule.styles
            };
        });

    return <IThemeModule[]>(<unknown>await Promise.all(themesPromises));
};

/**
 * Get theme definition extensions
 * @param themeModuleName name of the theme module
 * @param themeModuleBinder theme module binder object
 */
export const getThemeDefinitionExtensions = async (
    themeModuleName: string,
    themeModuleBinder: IModuleRegistration
): Promise<IDictionary<IModuleDefinition>> => {
    let themeDefinitionExtensions: IDictionary<IModuleDefinition> = <IDictionary<IModuleDefinition>>{};

    // In case of extended theme module, read parent theme definiton extensions first
    if (themeModuleBinder.parentDefinitionPath) {
        const dapiHelper = getModulesDAPIHelperSingleton();
        const parentThemeName = dapiHelper.getModuleName(themeModuleBinder.parentDefinitionPath);
        const parentThemeBinder = dapiHelper.getModuleBinder(parentThemeName);
        parentThemeBinder.definitionExtensions = parentThemeBinder.definitionExtensions || [];
        for (const moduleName of parentThemeBinder.definitionExtensions) {
            const pathToDefinition = path.join(
                parentThemeBinder.moduleDirectory || '',
                THEME_MODULE.DEF_EXTENSIONS,
                `${moduleName}${THEME_MODULE.DEF_EXT_FILE}`
            );

            const defExtensionJson: IModuleDefinition = <IModuleDefinition>((await safeReadJson(pathToDefinition)) || {});
            addDefaultModuleConfigProperties(defExtensionJson);
            themeDefinitionExtensions[moduleName] = <IModuleDefinition>await resolveRef(defExtensionJson, pathToDefinition);
        }
    }

    // Read theme module definition extensions
    themeModuleBinder.definitionExtensions = themeModuleBinder.definitionExtensions || [];
    for (const moduleName of themeModuleBinder.definitionExtensions) {
        const pathToDefinition = path.join(
            themeModuleBinder.moduleDirectory || '',
            THEME_MODULE.DEF_EXTENSIONS,
            `${moduleName}${THEME_MODULE.DEF_EXT_FILE}`
        );

        const defExtensionJson: IModuleDefinition = <IModuleDefinition>((await safeReadJson(pathToDefinition)) || {});
        addDefaultModuleConfigProperties(defExtensionJson);
        const themeLevelDefinitionExtensions = await resolveRef(defExtensionJson, pathToDefinition);
        themeDefinitionExtensions[moduleName] = _merge({}, themeDefinitionExtensions[moduleName], themeLevelDefinitionExtensions);
    }

    // Read partner level definition extensions
    const partnerDefinitionExtensions =
        (await safeGetAllFilesPath(
            path.join(keystonePaths.KEYSTONE_THEMES_DIR, themeModuleName, THEME_MODULE.DEF_EXTENSIONS, `*${THEME_MODULE.DEF_EXT_FILE}`)
        )) || [];
    for (const pathToDefExt of partnerDefinitionExtensions) {
        // Check if partner has definition extension override for this theme and merge
        if (await safeFileExists(pathToDefExt)) {
            const partnerDefExtensionJson = (await safeReadJson(pathToDefExt)) || {};
            const moduleName = path.basename(pathToDefExt, THEME_MODULE.DEF_EXT_FILE);
            themeDefinitionExtensions[moduleName] = _merge(themeDefinitionExtensions[moduleName], partnerDefExtensionJson);
        }
    }

    // Check and remove config properties present from config of theme definition extension, if duplicate property exists in dependncies of definition extension
    themeDefinitionExtensions = filterConfigProperties(themeDefinitionExtensions);
    return themeDefinitionExtensions;
};

/**
 * @function {getThemeSettings}   - Method to return partner theme settings
 */
export const getThemeSettings = async (moduleRegistrations: IDictionary<IModuleRegistration>) => {
    const themeModules: IModuleRegistration[] = (Object.keys(moduleRegistrations) || [])
        .filter(moduleType => moduleRegistrations[moduleType].$type === THEME_MODULE_TYPE)
        .map(moduleType => moduleRegistrations[moduleType]);

    // tslint:disable-next-line:no-any
    const themeSettings: any = {};
    await Promise.all(
        themeModules.map(async themeModule => {
            let settings = {};
            if (themeModule.themeSettings) {
                settings = (await safeReadJson(resolve(themeModule.moduleDirectory || '', themeModule.themeSettings))) || {};
            }

            if (themeModule.parentDefinitionPath) {
                const parentModuleName = getModuleName(themeModule.parentDefinitionPath);
                const parentSettingsPath = join(
                    dirname(themeModule.parentDefinitionPath),
                    `${parentModuleName}${THEME_MODULE.SETTINGS_FILE}`
                );
                if (await safeFileExists(parentSettingsPath)) {
                    settings = _merge(settings, (await safeReadJson(parentSettingsPath)) || {});
                }
            }

            let overrideSettings = {};
            const overrideSettingsFile = resolve(
                keystonePaths.KEYSTONE_THEMES_DIR,
                themeModule.name,
                `${themeModule.name}${THEME_MODULE.SETTINGS_FILE}`
            );
            if (await safeFileExists(overrideSettingsFile)) {
                overrideSettings = (await safeReadJson(overrideSettingsFile)) || {};
            }

            themeSettings[themeModule.name] = _merge(settings, overrideSettings);
        })
    );

    return themeSettings;
};

/**
 * Adds any default properties the module config should have
 * to the dapi response
 *
 * @param result List of modules
 */
const addDefaultModuleConfigProperties = (defJson: IModuleDefinition) => {
    const type = defJson.type || defJson.$type;
    if (defJson && (type === 'contentModule' || type === 'containerModule' || type === 'pageModule' || type === 'definitionExtension')) {
        // Every module should have a class name property added if missing from the definition json
        if (!defJson.config) {
            defJson.config = {};
        }
        if (!defJson.config.className) {
            defJson.config.className = classNameDefnProperty;
        } else {
            // if className is defined ensure type is set to 'css'
            defJson.config.className.type = 'css';
        }
    }
};

/**
 * Filters config properties present from config of Definition extension, if same property exists in dependncies of definition extension
 * @param themeDefinitionExtensions Final response of theme definition extension
 */
const filterConfigProperties = (themeDefinitionExtensions: IDictionary<IModuleDefinition>) => {
    const properties = Object.keys(themeDefinitionExtensions);
    if (properties.length > 0) {
        for (let i = 0; i < properties.length; i++) {
            const definitionExtension: IModuleDefinition = themeDefinitionExtensions[properties[i]];
            const config = definitionExtension && definitionExtension[`config`];
            if (config && definitionExtension[`dependentSchemas`]) {
                const dependentSchemas = definitionExtension?.dependentSchemas;
                if (dependentSchemas) {
                    const keys = Object.keys(dependentSchemas);
                    if (keys && keys.length > 0) {
                        keys.forEach(p => {
                            const oneOf = dependentSchemas[p][`oneOf`];
                            const dependentProperty = dependentSchemas[p][`properties`];
                            // @ts-ignore
                            if (oneOf && oneOf.length > 0) {
                                // @ts-ignore
                                for (let j = 0; j < oneOf.length; j++) {
                                    // @ts-ignore
                                    const extendedproperties = oneOf[j]?.properties;
                                    if (extendedproperties) {
                                        checksAndRemovesConfigPropertyIfExisits(extendedproperties, config);
                                    }
                                }
                            }
                            if (dependentProperty) {
                                checksAndRemovesConfigPropertyIfExisits(dependentProperty, config);
                            }
                        });
                    }
                }
            }
        }
    }
    return themeDefinitionExtensions;
};

/**
 * Checks if same config properties exisits in dependentSchema and config of definition extension and remove that property from config
 * @param properties
 * @param config
 */
const checksAndRemovesConfigPropertyIfExisits = (properties: IDictionary<string>, config: IDictionary<IModuleProperty>) => {
    for (const propertyKey of Object.keys(properties)) {
        if (config[propertyKey]) {
            delete config[propertyKey];
        }
    }
};

/**
 * Gets the pages in the given theme module
 * @param themeModuleBinder binder for the theme module object
 */
const getThemePages = async (themeModuleBinder: IModuleRegistration) => {
    const pageNames = Object.keys(themeModuleBinder.pages || {});
    const themePages: IDictionary<IDictionary<object>> = {};
    for (const pageName of pageNames) {
        const layouts = themeModuleBinder.pages![pageName] || [];
        for (const layout of layouts) {
            const pathToPageLayout = path.join(
                themeModuleBinder.moduleDirectory || '',
                THEME_MODULE.LAYOUTS,
                THEME_MODULE.PAGES,
                pageName,
                `${layout}${THEME_MODULE.PAGE_FILE}`
            );
            const pageLayoutJson = (await safeReadJson(pathToPageLayout)) || {};
            themePages[pageName] = themePages[pageName] || {};
            themePages[pageName][layout] = await resolveRef(pageLayoutJson, pathToPageLayout);
        }
    }

    return themePages;
};

/**
 * Gets the segments in the given theme module
 * @param themeModuleBinder binder for the theme module object
 */
const getThemeSegments = async (themeModuleBinder: IModuleRegistration) => {
    const segments: IDictionary<object> = {};
    themeModuleBinder.segments = themeModuleBinder.segments || [];
    for (const segment of themeModuleBinder.segments) {
        const pathToSegment = path.join(
            themeModuleBinder.moduleDirectory || '',
            THEME_MODULE.LAYOUTS,
            THEME_MODULE.SEGMENTS,
            `${segment}${THEME_MODULE.SEGMENT_FILE}`
        );
        const segmentJson = (await safeReadJson(pathToSegment)) || {};
        segments[segment] = await resolveRef(segmentJson, pathToSegment);
    }

    return segments;
};

/**
 * Get default theme style presets definition extensions
 * @param themeModuleName name of the theme module
 * @param themeModuleBinder theme module binder object
 */
// tslint:disable-next-line:no-any
const getDefaultThemeStylePreset = async (themeModuleName: string, themeModuleBinder: IModuleRegistration): Promise<any> => {
    const pathToThemeStylesDefinition = path.join(
        themeModuleBinder.moduleDirectory || '',
        THEME_MODULE.STYLES,
        `${themeModuleName}${THEME_MODULE.STYLE_PRESET_DEFINITION_FILE}`
    );

    const themeStylePresets = await resolveRef((await safeReadJson(pathToThemeStylesDefinition)) || {}, pathToThemeStylesDefinition);
    const pathToPartnerThemeStylePresets = path.join(
        keystonePaths.KEYSTONE_THEMES_DIR,
        themeModuleName,
        THEME_MODULE.STYLES,
        `${themeModuleName}${THEME_MODULE.STYLE_PRESET_DEFINITION_FILE}`
    );

    const partnerThemeStylesDefinition = await resolveRef(
        (await safeReadJson(pathToPartnerThemeStylePresets)) || {},
        pathToPartnerThemeStylePresets
    );
    return _merge(themeStylePresets, partnerThemeStylesDefinition);
};

/**
 * Get additional theme style presets extensions
 * @param themeModuleName name of the theme module
 * @param themeModuleBinder theme module binder object
 */
// tslint:disable-next-line:no-any
const getThemeStylePresetInstances = async (themeModuleName: string, themeModuleBinder: IModuleRegistration): Promise<any> => {
    const mergedFiles: IDictionary<object> = {};
    const keystoneThemeFilePath = path.join(keystonePaths.KEYSTONE_THEMES_DIR, themeModuleName, THEME_MODULE.STYLES);

    const getThemeFilePaths = (
        await safeGetAllFilesPath(
            path.join(themeModuleBinder.moduleDirectory || '', THEME_MODULE.STYLES, `/*${THEME_MODULE.STYLE_PRESET_INSTANCE_FILE}`)
        )
    ).filter(p => !p.endsWith(THEME_MODULE.STYLE_PRESET_DEFINITION_FILE));

    const getKeystoneFilePaths = safeGetAllFilesPathSync(
        path.join(keystoneThemeFilePath, `/*${THEME_MODULE.STYLE_PRESET_INSTANCE_FILE}`)
    ).filter(p => !p.endsWith(THEME_MODULE.STYLE_PRESET_DEFINITION_FILE));

    Array.prototype.push.apply(getThemeFilePaths, getKeystoneFilePaths);

    if (getThemeFilePaths.length > 0) {
        for (let i = 0; i < getThemeFilePaths.length; i++) {
            const stylePresetName = path.basename(getThemeFilePaths[i], '.scss.json');
            const additionalThemeStylePresets = (await safeReadJson(getThemeFilePaths[i])) || {};

            mergedFiles[stylePresetName] = _merge(mergedFiles[stylePresetName], additionalThemeStylePresets);
        }
    }

    return mergedFiles;
};
